<template>
  <link href="//cdn.bootcss.com/bootstrap/3.3.6/css/bootstrap.min.css" rel="stylesheet">
<vue-chart
    :chart-type="chartType"
    :columns="columns"
    :rows="rows"
    :options="options"
></vue-chart>


  <table class="table .table-condensed">
    <th>Weather</th>
    <th>Temp</th>
    <th>Time</th>
    <tr v-for="item in weather.list">
      <td>{{item.weather[0].main  }}</td>
      <td>{{item.main.temp  }}</td>
      
      <td>{{ (item.dt_txt) }}</td>
    </tr>
    
  </table>

</template>

<script>


    import VueResource from 'vue-resource'



export default {

  
  data () {
    return {
      weather: '',
      handleResize:'',
      columns: [{
                    'type': 'string',
                    'label': 'Time'
                }, {
                    'type': 'number',
                    'label': 'Temp'
                }],
      rows: [],
                options: {
                    title: 'TEMP IN SIJI',
                    hAxis: {
                        title: 'Time',

                    },
                    vAxis: {
                        title: 'Temp',
                        minValue: 15,
                        maxValue: 30
                    },
                    width: 900,
                    height: 500,
                    curveType: 'function'
                }

    }
  },
  
  
  ready: function () {
    this.fetchData()
  },


  computed: {
  

  },


  methods: {
    fetchData: function () {


        //window.addEventListener('resize', this.handleResize);

    
        this.$http.get("http://api.openweathermap.org/data/2.5/forecast?q=Siji&appid=26fb858cc56daf127768c1b6bc4098a2&units=metric").then(function(response){


      this.weather = response.data
      
      for(var i in this.weather.list){
        let item = this.weather.list[i];
        let datetime = item.dt_txt;
        this.rows.push([datetime,item.main.temp]);

      }

        this.options.width= window.innerWidth*0.8;
        this.options.height= window.innerHeight*0.8;

/*
      this.weather.list.forEach(function(item) {        
          this.rows.push( [item.dt,item.main.temp])});
*/

      console.log(this.rows);

    },function(error){
      console.log(error);
    })  

    }




  }
  
}
</script>




